import React from 'react'
import { XYCoord, DragLayer } from 'react-dnd'
import { Identifier } from 'dnd-core'
import ItemTypes from '@/constants/item-types'
import SnippetDragPreview from '@/components/snippet-drag-preview'
import { canMoveSnippet } from './snippet-utils'

const layerStyles: React.CSSProperties = {
  position: 'fixed',
  pointerEvents: 'none',
  zIndex: 100,
  left: 0,
  top: 0,
  width: '100%',
  height: '100%',
}

interface DragLayerProps {
  item?: any
  itemType?: Identifier | null
  initialOffset?: XYCoord | null
  currentOffset?: XYCoord | null
  isDragging?: boolean
}

function getItemStyles(props: DragLayerProps) {
  const { initialOffset, currentOffset } = props

  if (!initialOffset || !currentOffset) {
    return {
      display: 'none',
    }
  }

  const { x, y } = currentOffset

  const transform = `translate(${x}px, ${y}px)`
  return {
    transform,
    WebkitTransform: transform,
  }
}

const SnippetDragLayer: React.FC<DragLayerProps> = props => {
  const { itemType, isDragging, currentOffset } = props

  function renderItem({ canDrop }: { canDrop: boolean }) {
    switch (itemType) {
      case ItemTypes.BOARD:
      case ItemTypes.SNIPPET:
        return <SnippetDragPreview canDrop={canDrop} />
      default:
        return null
    }
  }

  if (!isDragging) {
    return null
  }

  if (!currentOffset) {
    return null
  }

  const element = document.elementFromPoint(currentOffset.x, currentOffset.y)

  let canDrop = false
  if (element) {
    canDrop = canMoveSnippet(element)
  }

  return (
    <div className="report-template-builder__drag-layer" style={layerStyles}>
      <div style={getItemStyles(props)}>{renderItem({ canDrop })}</div>
    </div>
  )
}

export default DragLayer(monitor => ({
  item: monitor.getItem(),
  itemType: monitor.getItemType(),
  initialOffset: monitor.getInitialSourceClientOffset(),
  currentOffset: monitor.getSourceClientOffset(),
  isDragging: monitor.isDragging(),
}))(SnippetDragLayer)
